<template>
  <v-container
    fluid
    grid-list-md>
    <v-layout
      row
      wrap>
      <v-flex md12>
        <material-card
          color="primary"
          title="Auditoria">
          <v-card class="elevation-0">
            <v-form
              @submit.prevent="pesquisar">
              <v-card-text>
                <v-layout
                  row
                  wrap>
                  <v-flex md5>
                    <v-autocomplete
                      v-model="filtros.usuario"
                      :items="usuarios"
                      label="Usuário"
                      item-value="id"
                      item-text="descricao"
                      clearable
                    />
                  </v-flex>
                  <!--
                  <v-flex md2>
                    <v-text-field
                      v-mask="'###.###.###-##'"
                      v-model="filtros.cpf"
                      label="CPF Cliente"
                    />
                  </v-flex>
                  -->
                  <v-flex md3>
                    <v-text-field
                      v-model="filtros.dataInicial"
                      label="Data Inicial"
                      type="date"
                    />
                  </v-flex>
                  <v-flex md1>
                    <span class="layout fill-height align-center justify-center">a</span>
                  </v-flex>
                  <v-flex md3>
                    <v-text-field
                      v-model="filtros.dataFinal"
                      label="Data Final"
                      type="date"
                    />
                  </v-flex>
                  <v-flex md5>
                    <v-autocomplete
                      v-model="filtros.entity"
                      :items="entities"
                      item-value="id"
                      item-text="descricao"
                      label="Operação"
                    />
                  </v-flex>
                </v-layout>
              </v-card-text>
              <v-card-actions>
                <v-spacer />
                <v-btn
                  :loading="loadingBtn"
                  color="primary"
                  type="submit">Pesquisar</v-btn>
              </v-card-actions>
            </v-form>
          </v-card>
        </material-card>
      </v-flex>
      <v-flex md12>
        <v-card>
          <v-toolbar
            flat
            color="white">
            <v-toolbar-title>Auditorias</v-toolbar-title>
            <v-divider
              class="mx-2"
              inset
              vertical />
          </v-toolbar>
          <v-progress-linear
            :active="loading"
            color="light-blue"
            indeterminate
            height="3"
          />
          <v-data-table
            :headers="headers"
            :items="items"
            :total-items="totalItems"
            :pagination.sync="pagination"
            :rows-per-page-items="pagination.rowsPerPageItems"
            :rows-per-page-text="pagination.rowsPerPageText"
            item-key="rev_id"
            class="elevation-1 arrow-pagintion"
          >
            <template
              slot="items"
              slot-scope="{ item }"
              ma-5>
              <td class="text-xs-center">{{ item['rev_data'] | moment("DD/MM/YYYY H:mm") }}</td>
              <td class="text-xs-left">{{ item['rev_usuario'] }}</td>
              <td class="text-xs-left">{{ item['rev_operacao'] }}</td>
              <td class="text-xs-left">
                <span v-html="printDetalhe(item)" />
              </td>
            </template>
          </v-data-table>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>
<script>
import { AuditoriaBusiness, TiposBusiness } from '../../business'
import { MONEY, ROWS_PER_PAGE_ITEMS, TODAS_ID } from '../../constants'
import DateUtils from '../../utils/dateUtils'

export default {
  metaInfo: {
    titleTemplate: '%s - Auditoria'
  },
  data () {
    return {
      money: MONEY,
      pagination: {
        rowsPerPage: 15,
        rowsPerPageItems: ROWS_PER_PAGE_ITEMS
      },
      filtros: {
        entity: null,
        dataInicial: DateUtils.addDia(new Date(), -1),
        dataFinal: DateUtils.currentDate(),
        usuario: null,
        cpfCliente: null,
        exibirDetalhes: false,
        operacao: TODAS_ID
      },
      headers: [
        {
          sortable: false,
          text: 'Data/Hora',
          align: 'center',
          value: 'data'
        },
        {
          sortable: false,
          text: 'Usuário',
          align: 'left',
          value: 'usuario'
        },
        {
          sortable: false,
          text: 'Operação',
          value: 'valorVenda'
        },
        {
          sortable: false,
          text: 'Detalhes',
          value: 'detalhes'
        }
      ],
      items: [],
      totalItems: null,
      loading: false,
      loadingBtn: false,
      usuarios: [],
      operacoes: [],
      entities: []
    }
  },
  watch: {
    pagination: {
      handler () {
        this.paginar()
      }
    }
  },
  beforeMount () {
    this.loading = true
    var promiseUsuarios = TiposBusiness.getAllUsuarios()
      .then(response => {
        this.usuarios = response.data
      })
    var promiseEntities = TiposBusiness.getAllEntitiesAuditaveis()
      .then(response => {
        this.entities = this.entities.concat(response.data)
      })

    var promiseOperacoesAuditoria = TiposBusiness.getAllOperacoesAuditaveis()
      .then(response => {
        this.operacoes.push({ id: TODAS_ID, descricao: 'Todas' })
        this.operacoes = this.operacoes.concat(response.data)
      })

    Promise.all([
      promiseUsuarios,
      promiseEntities,
      promiseOperacoesAuditoria
    ]).finally(() => {
      this.loading = false
    })
  },
  methods: {
    pesquisar () {
      if (!this.filtros.entity) {
        this.$root.showAlerta('Informe uma Operação')
        return
      }
      this.loadingBtn = true
      this.paginar()
    },
    paginar () {
      if (!this.filtros.entity) {
        return
      }
      this.loading = true
      AuditoriaBusiness.paginationAuditoria(this.pagination.rowsPerPage, this.pagination.page - 1, this.pagination.sortBy, this.filtros)
        .then(response => {
          this.items = response.data.content
          this.totalItems = response.data.totalElements
        }).catch(error => {
          this.$root.showErro(error.data)
        })
        .finally(() => {
          this.loadingBtn = false
          this.loading = false
        })
    },
    printDetalhe (item) {
      let detalhes = ''
      Object.keys(item).forEach(key => {
        if (!['senha', 'rev_operacao', 'rev_data', 'rev_usuario', 'rev_id'].includes(key)) {
          if (key.indexOf('data') > -1) {
            detalhes += `<b>${key}: </b>`
            if (item[key]) {
              detalhes += this.$moment(item[key]).format('DD/MM/YYYY')
            }
          } else {
            detalhes += `<b>${key}: </b>`
            if (item[key]) {
              detalhes += item[key]
            }
          }
          detalhes += ` <br>`
        }
      })
      return detalhes
    }
  }
}
</script>
